.role-new {
  padding-left: 20px;
  .el-input {
    width: 300px;
  }
  .el-radio,
  .el-checkbox {
    height: 40px;
    line-height: 40px;
  }
  .list {
    display: flex;
    height: 40px;
    line-height: 40px;
    margin-bottom: 20px;
    span {
      display: inline-block;
      height: 40px;
      line-height: 40px;
    }
    .list-text {
      display: inline-block;
      font-size: 12px;
      margin-left: 40px;
      margin-top: 1px;
      color: gray;
    }
    &.manage {
      height: inherit;
      line-height: inherit;
      .title {
        height: 40px;
        line-height: 40px;
        font-weight: 600;
      }
    }
    &.submit {
      display: inline-block;
      text-indent: 5em;
    }
  }
}
</style>